.about_client {
  padding-top: 80px;
}
.about_client h3 {
  padding-bottom: 18px;
  font-size: 22px;
  font-weight: bold;
}
.about_client section {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.about_client dl {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 22%;
  border: 1px solid #d2d7e1;
  box-shadow: 0 4px 4px rgba(131, 145, 161, 0.16);
  background-color: #fff;
  border-radius: 8px;
}
.about_client dt {
  order: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 100%;
  height: 42px;
  line-height: 50px;
  background-color: #f7fafd;
  color: #333;
  font-size: 15px;
  font-weight: 600;
  text-align: center;
  border-top: 1px solid #d2d7e1;
  border-radius: 0 0 8px 8px;
}
.about_client dt img {
  width: 24px;
}
.about_client dd {
  display: flex;
  justify-content: center;
  padding: 20px 0;
  width: 100%;
}
.about_client dd img {
  width: 80%;
}
.domestic {
  margin-bottom: 80px;
}
